import {
	pathToBase64
} from '../../components/an-uploadImg/js_sdk/gsq-image-tools/image-tools/index.js'
export default {
	data() {
		return {
			title: 'Hello',
			date: '2018-12-25',
			time: '12:01',
			textareaAValue: '',
			two: '啦啦啦',
			twolei: '略略略',
			ddd: '',
			idasd:'我不想看到你',

		}
	},
	onLoad() {

	},
	mounted() {
		localStorage.setItem("key","我爱你")
		this.idasd = localStorage.getItem("key")
	},
	methods: {
		
		DateChange(e) {
			this.date = e.detail.value
		},
		TimeChange(e) {
			this.time = e.detail.value
		},
		textareaBInput(e) {
			this.textareaAValue = e.detail.value
		},
		twosss() {
			var that = this
			uni.scanCode({
				success: function(res) {

					that.$store.state.weburl = res.result
					if (res.result.slice(0, 7) == 'http://' || res.result.slice(0, 7) == 'https:/') {
						that.two = '一条链接: ' + res.result

					} else {
						that.two = '一段文字: ' + res.result
					}
					that.twolei = res.result.split('?')[0]
					that.ddd = that.two + that.twolei
				}
			});
		}
	}
}
